import './App.css'

import { Observer } from 'mobx-react-lite'
import React from 'react'

import { ConnectionWarning } from './atoms/ConnectionWarning'
import Pacman from './atoms/Pacman'
import { useRoot } from './atoms/RootProvider'
import { UpdateHandler } from './atoms/UpdateHandler'
import { DefaultErrorHandler } from './organisms/DefaultErrorHandler'
import { Routing } from './routes/Routing'
import { UserBootstrap } from './segments/login/organisms/UserBootstrap'

export const App: React.FC = () => {
  const { user } = useRoot()
  return (
    <DefaultErrorHandler>
      <UserBootstrap />
      <ConnectionWarning />
      <UpdateHandler>
        <Observer>{() => (user.isReady ? <Routing /> : <Pacman />)}</Observer>
      </UpdateHandler>
    </DefaultErrorHandler>
  )
}
