import './enableDebug'

import { useSentryInit } from '@speedlo/tools'
import React from 'react'
import { render } from 'react-dom'

import { App } from './App'
import { RootProvider } from './atoms/RootProvider'
import { Router } from './atoms/Router'
import { SnackProvider } from './atoms/SnackProvider'
import { ThemeProvider } from './atoms/ThemeProvider'
import { appConfig } from './config'
import { I18nManager } from './i18n'

const Main = () => {
  useSentryInit(appConfig.sentryDSN)
  return (
    <ThemeProvider>
      <RootProvider>
        <I18nManager>
          <Router>
            <SnackProvider>
              <App />
            </SnackProvider>
          </Router>
        </I18nManager>
      </RootProvider>
    </ThemeProvider>
  )
}

const rootElement = document.getElementById('root')
render(<Main />, rootElement)
